export default{
  //   window.location.origin
  getUrl (perUrl) {
    let url = ''
    let link = window.location.origin
    let test = link.indexOf('localhost:8080')
    let prod = link.indexOf('28dagang.com')
    // test 
    if (test != -1 ) {
      url = 'http://localhost:8080/#/' + perUrl
    }
    // 
    if (test == -1 && prod != -1) {
      url = 'http://www.28dagang.com/#/' + perUrl
    }
    return url
  }
}



